import React, {Component, Fragment} from 'react';
import {BrowserRouter as Router , Route, Link} from 'react-router-dom';

// pages
import Navbar from './Navbar';
import Slidebar from './SlideBar';
import Dashboard from '../Pages/Dashboard/Dashboard';
import Report from '../Pages/Report/Report';
import Approval from '../Pages/MemberManajer/Approval';
import DataBSI from '../Pages/DataMaster/DataBSI';
import DataBSU from '../Pages/DataMaster/DataBSU';
import Sampah from '../Pages/DataMaster/Sampah';
import DataPetugas from '../Pages/PetugasManajer/DataPetugas';


class Home extends Component{
    render(){
        return(
            <Fragment>
            <Router>

                <Navbar /> 
                <Slidebar /> 
                
                <Route path='/' exact component={Dashboard}  />
                <Route path="/approval"  component={Approval} />
                <Route path="/Report" component={Report} />  
                <Route path='/data-bsi' component={DataBSI} />
                <Route path='/data-bsu' component={DataBSU} />
                <Route path='/sampah' component={Sampah} />
                <Route path='/data-petugas' component={DataPetugas} />
            </Router>
            
            </Fragment>
        )
    }
}

export default Home; 