import React, {Component, Fragment} from 'react';
import axios from 'axios';

import swal from 'sweetalert';

let Petugas = (props) => {

    if (props.status == '2') {
       return (
            <tr>
              <td>{props.no}</td>
                <td>{props.nama}</td>
                <td>{props.noHp}</td>
                <td>{props.email}</td>
                <td>{props.kurir} </td>
                <td>{props.level}</td>
                <td> {props.role}    </td>
                <td>
                    Terhapus
                </td>

                <td> 
                    <button  type="button" className="btn-sm btn-success " onClick={() => props.restore(props.id)}><i className="mdi mdi-backup-restore  " > </i></button>
                    </td> <td> 
                    <button   type="button" className="btn-sm  btn-danger" onClick={() => props.deletee(props.id)}><i className="mdi mdi-delete-forever" ></i></button>

                    </td>  
            </tr>
            )
    }else{
        let status = '';
        if (props.status == "0") {
            status = "Belum Aktif"
        }else if(props.status == "1"){
            status = "Aktif"
        }else{
            status = "Terhapus"
        }

    if (props.statusChange == 'aktif') {
        if (props.status == '1') {
            return (
                <tr>
                  <td>{props.no}</td>
                    <td>{props.nama}</td>
                    <td>{props.noHp}</td>
                    <td>{props.email}</td>
                    <td>{props.kurir} </td>
                    <td>{props.level}</td>
                    <td> {props.role}    </td>
                    <td>
                        {status}
                    </td>

                    <td> 

                        <button type="button" className="btn-sm btn-info" ><i className="mdi mdi-check-circle"   > </i></button></td>
                        <td>
                        <button   type="button" className="btn-sm  btn-warning" onClick={() => props.romove(props.id)}><i className="mdi mdi-delete-forever " ></i></button>

                        </td>  
                </tr>
            )
        }else{
            return null
        }
        }else if(props.statusChange == 'blm'){
            if (props.status == '0') {
                return (
                    <tr>
                      <td>{props.no}</td>
                        <td>{props.nama}</td>
                        <td>{props.noHp}</td>
                        <td>{props.email}</td>
                        <td>{props.kurir} </td>
                        <td>{props.level}</td>
                        <td> {props.role}    </td>
                        <td>
                            {status}
                        </td>

                        <td> 
                          <button type="button" className="btn-sm btn-info" ><i className="mdi mdi-check-circle"   > </i></button></td>
                        <td>
                        <button   type="button" className="btn-sm  btn-warning" onClick={() => props.romove(props.id)}><i className="mdi mdi-delete-forever " ></i></button>

                            </td>  
                    </tr>
                    )
            }else{
                return null
            }
        }else{
             return (
                <tr>
                  <td>{props.no}</td>
                    <td>{props.nama}</td>
                    <td>{props.noHp}</td>
                    <td>{props.email}</td>
                    <td>{props.kurir} </td>
                    <td>{props.level}</td>
                    <td> {props.role}    </td>
                    <td>
                        {status}
                    </td>

                    <td> 
                       <button type="button" className="btn-sm btn-info" ><i className="mdi mdi-check-circle"   > </i></button></td>
                        <td>
                        <button   type="button" className="btn-sm  btn-warning" onClick={() => props.romove(props.id)}><i className="mdi mdi-delete-forever " ></i></button>

                        </td>  
                </tr>
            )
        }
    }

    
    }

   


class DataPetugas extends Component{

    state = {
        petugas: [],
        statusChange : 'all'
    }

    getApi = () => {
        axios.get('https://sampun.alphacstudios.com/api/data/petugas')
            .then((res) => {
                this.setState({
                    petugas: res.data
                })
                console.log(res)
            })
    }

    hendeleChange = (event) => {
        console.log(event.target.value)
        this.setState({
            statusChange: event.target.value
        })
    }
    componentDidMount(){
        document.title = "Approval"
        this.getApi()
    }

    hendleApprov = (data) => {
        swal("Berhasil!!", "Petugas Telah AKtif", "success");
        console.log(data)
        axios.get(`https://sampun.alphacstudios.com/api/data/petugas/approve/${data}`)
        .then(res => {
            this.getApi()
        })
    }

    hendeleRestore = (data) => {
       swal("Berhasil!!", "Data Telah DiKembalikan", "success");
         axios.get(`https://sampun.alphacstudios.com/api/data/petugas/approve/aktif/${data}`)
        .then(res => {
            this.getApi()
        })
    }

    hendleDeletePer = (data) => {
        swal("Berhasil!!", "Data DI Hapus Permanen", "success");
        console.log(data)
        axios.get(`https://sampun.alphacstudios.com/api/data/petugas/deletep/${data}`)
        .then(res => {
            this.getApi()
        })
    }
    hendleRemove = (data) => {
        swal("Berhasil!!", "Data DI Hapus Sementara", "success");
        axios.get(`https://sampun.alphacstudios.com/api/data/petugas/approve/nonaktif/${data}`)
        .then(res => {
            this.getApi()
        })
    }

	render(){
        let i = 0;
        let j = 0;
		return (
			  <div id="page-wrapper">
            <div className="container-fluid">
                <div className="row bg-title">
                    <div className="col-lg-3 col-md-4 col-sm-4 col-xs-12">
                        <h4 className="page-title">Data Member</h4> 
                    </div>
                </div>

               
               </div>

               <div className="white-box">
                            <div className="card-body">
                                <h4 className="card-title">Data Member</h4>
                                
                                <div className="panel-group" id="accordion">
                                    <div className="panel panel-default">
                                        <div className="panel-heading">

                                        <div className="row">
                                            <div className="col col-md-4">
                                                 <div className="form-group">
                                                    <label >Katagori</label>
                                                    <select className="form-control" onChange={this.hendeleChange}>
                                                      <option value="all">All</option>
                                                      <option value="blm">Yang Belum Aktif</option>
                                                      <option value="aktif">Aktif</option>
                                                    </select>
                                                  </div>
                                            </div>
                                            
                                        </div>
                                             
                                        </div>
                                        <div id="collapseOne" className="panel-collapse collapse in">
                                            <div className="panel-body">
                                                <div className="card-body">
                                                    <div className="table-responsive">
                                                        <table id="demo-foo-accordion" className="table table-bordered m-b-0 toggle-arrow-tiny" data-filtering="true" data-paging="true" data-sorting="true">
                                                            <thead>
                                                                <tr className="footable-filtering">
                                                                    <th>No</th>
                                                                    <th>Nama</th>
                                                                    <th data-hide="phone"> No Hp </th>
                                                                    <th data-toggle="true">Email </th>
                                                                    <th data-hide="all"> Kurir </th>
                                                                    <th data-hide="all"> Level </th>
                                                                    <th > Role</th>
                                                                    <th > Status </th>
                                                                     <th  > Approv </th>
                                                                    <th > Hapus </th>
                                                                </tr>
                                                            </thead>
                                                            <tbody>
                                                         
                                                            {
                                                                Object.keys(this.state.petugas).map((key, index) => {
                                                                     if (this.state.petugas[key].status != '2') {
                                                                    return <Petugas key={index} nama={this.state.petugas[key].nama} email={this.state.petugas[key].namaemail} 
                                                                                noHp={this.state.petugas[key].hp} 
                                                                                kurir={this.state.petugas[key].kurir} level={this.state.petugas[key].level}
                                                                                role={this.state.petugas[key].role} status={this.state.petugas[key].status}
                                                                                statusChange={this.state.statusChange} id={key} 
                                                                                aproval={this.hendleApprov}  romove={this.hendleRemove} no={i = i + 1}
                                                                     />  
                                                                     }    
                                                                    
                                                              })    
                                                            }
                                                            </tbody>
                                                        </table>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    
                                </div>
                            </div>
                        </div>


                         <div className="white-box">
                            <div className="card-body">
                                <h4 className="card-title">Data Member Yang Dihapus</h4>
                                
                                <div className="panel-group" id="accordion">
                                    <div className="panel panel-default">
                                        <div className="panel-heading">

                                                                                
                                        </div>
                                        <div id="collapseOne" className="panel-collapse collapse in">
                                            <div className="panel-body">
                                                <div className="card-body">
                                                    <div className="table-responsive">
                                                        <table id="demo-foo-accordion" className="table table-bordered m-b-0 toggle-arrow-tiny" data-filtering="true" data-paging="true" data-sorting="true">
                                                            <thead>
                                                                <tr className="footable-filtering">
                                                                    <th>No</th>
                                                                    <th>Nama</th>
                                                                    <th data-hide="phone"> No Hp </th>
                                                                    <th data-toggle="true">Email </th>
                                                                    <th data-hide="all"> Kurir </th>
                                                                    <th data-hide="all"> Level </th>
                                                                    <th > Role</th>
                                                                    <th > Status </th> 
                                                                    <th > Kembalikan </th>
                                                                    <th > Hapus </th>
                                                                </tr>
                                                            </thead>
                                                            <tbody>
                                                               {
                                                                Object.keys(this.state.petugas).map((key, index) => {
                                                                     if (this.state.petugas[key].status == '2') {
                                                                        
                                                                        return <Petugas key={index} nama={this.state.petugas[key].nama} email={this.state.petugas[key].namaemail} 
                                                                                    noHp={this.state.petugas[key].hp} 
                                                                                    kurir={this.state.petugas[key].kurir} level={this.state.petugas[key].level}
                                                                                    role={this.state.petugas[key].role} status={this.state.petugas[key].status}
                                                                                    id={key} restore={this.hendeleRestore} deletee={this.hendleDeletePer}  no={j = j + 1}
                                                                                   
                                                                         />      
                                                                    }
                                                                    
                                                              })    
                                                            }
                                                                
                                                            </tbody>
                                                        </table>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    
                                </div>
                            </div>
                        </div>
               {/* <footer className="footer text-center"> 2019 &copy; Sampun </footer> */}
        </div>
		)
	}
}

export default DataPetugas;